import React, { Component } from 'react';
import $ from 'jquery';
import Loader from './loader';

export const LoadingComponent = ComposedComponent => class extends Component {
  constructor() {
    super();
    this.loadResponsesFromServer = this.loadResponsesFromServer.bind(this);
    this.state = { data: 'loading' };
  }
  loadResponsesFromServer(urlOverride) {
    const that = this;
    const firstLoad = this.state.data === 'loading';
    const url = (typeof urlOverride === 'undefined') ? this.props.url : urlOverride;
    $.ajax({
      url,
      dataType: 'json',
      success(data) {
        if (firstLoad) {
          // on first load, overwrite loading state
          that.setState({ data: data.results });
        } else {
          // if not first load, merge data
          const newMap = new Map();
          that.state.data.map(x => newMap.set(x.pk, x));
          data.results.map(x => newMap.set(x.pk, x));
          that.setState({ data: Array.from(newMap.values()) });
        }
        if (data.next) {
          // if there is another page, grab it
          that.loadResponsesFromServer(data.next);
        }
      },
      error(xhr, status, err) {
        console.error(url, status, err.toString());
      },
    });
  }
  componentDidMount() {
    this.loadResponsesFromServer();
    setInterval(this.loadResponsesFromServer, this.props.pollInterval);
  }
  render() {
    if (this.state.data === 'loading') {
      return <Loader />;
    }
    return (
      <ComposedComponent
        {...this.props}
        data={this.state.data}
        loadfromserver={this.loadResponsesFromServer}
      />
    );
  }
};



