import React, { Component } from 'react';
import ResponseCard from './response_card';

class ResponseWall extends Component {
  render() {
    let showAll = true;
    const that = this;
    const responseNodes = this.props.responses.map((response, index) => {
      let displayCard = !response.is_archived;
      if (displayCard) {
        // check if only showing some keywords
        showAll = that.props.keyword === null;
        if (!showAll) {
          displayCard = response.matched_keyword === that.props.keyword;
        }
      }
      if (!this.props.curating) {
        displayCard = response.display_on_wall;
      }
      if (displayCard) {
        return (
          <ResponseCard
            preview={that.props.preview}
            curating={that.props.curating}
            response={response}
            key={index}
            toggleCard={that.props.toggleCard}
          />
        );
      }
      return null;
    });
    let className = 'ui three cards';
    if (this.props.preview || !this.props.curate) {className = 'ui one cards';}
    return <div className={className}>{responseNodes}</div>;
  }
}

export default ResponseWall;



