import React, { Component } from 'react';
import $ from 'jquery';
import { LoadingComponent } from './refreshing_table';
import ResponseWall from './response_wall';

class CurateContainer extends Component {
  constructor(props) {
    super(props);
    this.toggleCard = this.toggleCard.bind(this);
    this.state = { data: props.data };
  }
  toggleCard(response) {
    const that = this;
    const tmp = response.display_on_wall ? 'false' : 'true';
    const data = that.state.data;
    data[that.state.data.indexOf(response)].display_on_wall = !response.display_on_wall;
    this.setState({
      data,
    });
    $.ajax({
      url: `/api/v1/sms/in/${response.pk}`,
      type: 'POST',
      data: {
        display_on_wall: tmp,
      },
      success() {
      },
      error(xhr, errmsg, err) {
        console.log(`${xhr.status}: ${xhr.responseText}`);
        console.log(err);
      },
    });
  }
  render() {
    if (this.props.curate) {
      return (
        <div className="ui stackable grid container">
          <div className="twelve wide column">
            <h4>Available Messages</h4>
            <ResponseWall
              curating
              toggleCard={this.toggleCard}
              keyword={this.props.keyword}
              responses={this.state.data}
            />
          </div>
          <div className="three wide column">
            <h4>Live Preview</h4>
            <ResponseWall
              curating={false}
              preview
              toggleCard={this.toggleCard}
              keyword={this.props.keyword}
              responses={this.state.data}
            />
          </div>
        </div>
      );
    }
    return (
      <div className="ui stackable grid fluid container">
        <div className="sixteen wide centered column">
          <ResponseWall
            curating={false}
            preview={false}
            toggleCard={this.toggleCard}
            keyword={this.props.keyword}
            responses={this.state.data}
          />
        </div>
      </div>
    );
  }
}

export default LoadingComponent(CurateContainer);



